.modal-90w{
  min-width: 90%;
}

label.required:after {
  color: red;
  content: " *";
  font-weight: bold;
}

p.requiredField {
  color: red;
  font-size: small;
}


.cluster {
  background: #2d84c8;
  border-radius: 50%;
  text-align: center;
  color: white;
  font-weight: 700;
  border: 1px solid #2d84c8;
  font-family: monospace;
}

.cluster:before {
  content: ' ';
  position: absolute;
  border-radius: 50%;
  z-index: -1;
  top: 1px;
  left: 1px;
  right: 1px;
  bottom: 1px;
  border: 1px solid white;
}

